html
  scroll-behavior: smooth
body
  // заменить ниже на свои значения
  @extend %body
  background-color: $bgc
  color: $text

html,
body
  height: 100%
  margin: 0

ul, li
  padding: 0
h1,h2,h3
  margin: 0
input
  outline: none

.wrapper
  display: flex
  flex-direction: column
  min-height: 100%
  overflow: hidden

.main
  flex: 1 0 auto

.footer
  flex: 0 0 auto

.container
  max-width: 1470px
  margin: 0 auto
  padding: 0 83px 0 50px

.image
  width: 100%
  height: 100%
  object-fit: contain

.logo--header
  height: 50px

.heading
  @extend %head

.btn-reset
  padding: 0
  border: none
  background-color: transparent
  cursor: pointer
  outline: none

.btn
  background: $black
  border-radius: 20px
  padding: 13px 90px
  color: $white

.unvisible
  position: absolute
  left: -3000px
  overflow: hidden
  width: 0
  height: 0
